<template>
  <div class="block">
    <el-select v-model="finish_state" placeholder="显示" @change="sorting">
      <el-option
        v-for="item in showOptions"
        :key="item.value"
        :label="item.label"
        :value="item.value"
      ></el-option>
    </el-select>
    <div class="radio">
      排序：
      <el-radio-group v-model="is_desc" @change="sorting">
        <el-radio :label="false">正序</el-radio>
        <el-radio :label="true">倒序</el-radio>
      </el-radio-group>
      <el-tooltip class="item" effect="dark" content="刷新任务列表" placement="top">
        <el-button circle @click="sorting" style="margin-left:1em">
          <i class="el-icon-refresh"></i>
        </el-button>
      </el-tooltip>
    </div>

    <transition
      name="fade"
      enter-active-class="animated swing"
      leave-active-class="animated bounce"
    >
      <el-timeline id="tasks" v-if="showList">
        <el-timeline-item
          v-for="task in resTaskList"
          :key="task.task_id"
          placement="top"
          :timestamp="timeFormat(task.create_time)"
          color="#0bbd87"
        >
          <el-card>
            <div slot="header" class="clearfix" style="font-weight:bold">
              <span style="margin-right:2em">任务：{{task.task_id}}</span>
              <span style="margin-right:1em">文件：{{task.file_name}}</span>
              <!-- <span>{{task.file_name}}</span> -->
              <el-button style="float: right; padding: 0.5em">
                <i class="el-icon-download"></i>
                <a :href="'/file/downloadfile?fileId='+task.file_id">下载</a>
              </el-button>
            </div>
            <div
              style="background-color:background-image: linear-gradient( 135deg, #ABDCFF 10%, #0396FF 100%);"
            >
              <span style="margin-right:1em">
                任务类型：
                <el-tag
                  effect="light"
                  :type="task.task_type==='video'?'':task.task_type==='audio'?'danger':'success'"
                >{{task.task_type==='video'?'视频':task.task_type==='audio'?'音频':'图片'}}</el-tag>
              </span>
              <span style="margin-right:1em">
                任务风格：
                <el-tag v-show="task.ims_name" effect="plain">{{task.ims_name ? task.ims_name:''}}</el-tag>&nbsp;
                <el-tag
                  v-show="task.aus_name"
                  effect="plain"
                  type="danger"
                >{{task.aus_name?task.aus_name:''}}</el-tag>
              </span>
              <span v-show="task.task_type!=='audio'" style="margin-right:1em">
                清晰度:
                <el-tag size="mini" effect="plain" hit>{{task.clarity}}</el-tag>
              </span>
              <span v-show="task.task_type==='video'">
                是否采用补帧加速:
                <span v-html="task.is_frame_speed?icon_true:icon_false"></span>
              </span>
              <span style="float:right;">
                <el-tag
                  effect="dark"
                  :type="!task.start_time?'waring':(!task.final_time)?'info':'success'"
                >{{!task.start_time?'正在排队...':(!task.final_time)?'正在生成..':'已完成'}}</el-tag>
              </span>
            </div>
          </el-card>
        </el-timeline-item>
      </el-timeline>
    </transition>
  </div>
</template>

<script>
import $ from "jquery";
export default {
  data() {
    return {
      user_id: this.$parent.userId,
      is_desc: true,
      finish_state: 0,
      showOptions: [
        {
          value: 0,
          label: "全部任务"
        },
        {
          value: -1,
          label: "未完成任务"
        },
        {
          value: 1,
          label: "已完成任务"
        }
      ],
      showList: true,
      resTaskList: [],
      icon_true: '<i style="color:#409EFF" class="el-icon-success"></i>',
      icon_false: '<i style="color:#409EFF" class="el-icon-error"></i>'
    };
  },
  created() {
    this.sorting();
  },
  methods: {
    // downloadFile: function(fileId) {
    //   $.ajax({
    //     url: "/file/downloadfile",
    //     type: "POST",
    //     async: false,
    //     data: {
    //       fileId: fileId
    //     }
    //   });
    // console.log(this);
    // console.log(fileId);
    // },
    timeFormat: function(string) {
      // let timeArr = str.split("-");
      // let data = timeArr[2].substring(0, 2);
      // let time = timeArr[0] + "/" + timeArr[1] + "/" + data;
      let str = new Date(string);
      function formatFunc(str) {
        //格式化显示
        return str > 9 ? str : "0" + str;
      }
      let year = str.getFullYear();
      let mon = formatFunc(str.getMonth() + 1);
      let day = formatFunc(str.getDate());
      let hour = str.getHours();
      let min = formatFunc(str.getMinutes());
      let time = year + "/" + mon + "/" + day + " " + " " + hour + ":" + min;
      return time;
    },
    sorting: function() {
      console.log("sort");
      let self = this;
      self.resTaskList = [];
      self.showList = false;
      $.ajax({
        url: "/task/list",
        type: "POST",
        // dataType: "json", //TODO:注释掉即可
        data: {
          user_id: self.user_id,
          is_desc: self.is_desc,
          finish_state: self.finish_state
        }
      })
        .done(function(resultData) {
          //首先清空任务列表div的内容

          let dataObj = resultData;
          // console.log(dataObj);
          // console.log(dataObj.error_code);
          self.resTaskList = dataObj.data;
          // $("#tasks").append(htmlContent);
        })
        .fail(function() {
          self.$swal({
            // toast: true,
            // position: 'top',
            showConfirmButton: false,
            timer: 3000,
            type: "warning",
            title: "请求出错，请稍等"
          });
          self.$router.replace("/");
        });
      self.showList = true;
    }
  }
};
</script>

